<template lang="pug">
  small.error-input(v-if='mensagem') {{ mensagem }}
  small.error-input(v-else) Preencha o campo
</template>

<style lang="scss">
.error-input {
  color: $cor-vermelho-claro;
}
</style>

<script>
export default {
  props: ['mensagem']
}
</script>





