<template lang="pug">
  section.registro-ocorrencia
    breadcrumb(:itens="breadcrumb")
    .container
      titulo-internas Onde aconteceu?
      p Informe o endereço onde aconteceu a ocorrência.
      .mapa
        maps-registro-ocorrencia
        p Marque um endereço no mapa ou pesquise no campo "Buscar endereço".
      titulo-internas Dados da ocorrência
      formulario-ocorrencia
</template>

<style lang="scss">
  .registro-ocorrencia {

    p {
      margin: 20px 0;
    }

    .mapa {
      margin-bottom: 30px;

      #map {
        height: 300px;
      }

      #registrar-ocorrencia-map {
        height: 350px;
      }

      p {
        background: $cor-azul-escuro;
        color: $cor-azul-claro;
        margin: 0;
        padding: 15px 15px;
      }
    }
  }
</style>

<script>
import TituloInternas from 'BASICS/TituloInternas'
import FormularioOcorrencia from 'MODULES/FormularioOcorrencia'
import Breadcrumb from 'BASICS/Breadcrumb'
import MapsRegistroOcorrencia from 'BASICS/MapsRegistroOcorrencia'

export default {
  components: {
    TituloInternas,
    FormularioOcorrencia,
    Breadcrumb,
    MapsRegistroOcorrencia
  },
  data() {
    return{
      breadcrumb: [
        { nome: 'A' },
        { nome: 'B' }
      ]
    }
  }
}
</script>







