import Vue from 'vue'
import Router from 'vue-router'
import Index from 'PAGES/Index'
import RegistrarOcorrencia from 'PAGES/RegistrarOcorrencia'
import PesquisarOcorrencia from 'PAGES/PesquisarOcorrencia'
import PainelOcorrencias from 'PAGES/PainelOcorrencias'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', name: 'Index', component: Index },
    { path: '/registrar-ocorrencia', name: 'RegistrarOcorrencia', component: RegistrarOcorrencia },
    { path: '/pesquisar-ocorrencia', name: 'PesquisarOcorrencia', component: PesquisarOcorrencia },
    { path: '/painel-ocorrencias', name: 'PainelOcorrencias', component: PainelOcorrencias }
  ]
})



