<template lang="pug">
  .ocorrencias-pesquisa
    .ocorrencia(v-for="ocorrencia in ocorrencias")
      .header
        h3 {{ocorrencia.titulo}}
        span {{ocorrencia.data}}
      .content
        p {{ocorrencia.conteudo}}
</template>

<script>
export default {
  props: ['ocorrencias']
}
</script>

<style lang="scss">
  .ocorrencias-pesquisa {
    padding: 10px 15px;
    .ocorrencia {
      border-bottom: 1px solid $cor-cinza-claro;
      padding: 10px 0;

      &:last-child {
        border: none;
      }

      .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        h3 {
          flex: 1;
          font-size: 16px;
        }

        span {
          font-size: 14px;
          opacity: 0.5;
        }
      }
    }
  }
</style>




