<template lang="pug">
  select.input-control(v-if="tipo == 'select'", @change="$emit('inputValor', $event.target)")
    option
    option(v-for="opcao in opcoes") {{ opcao }}
  input.input-control(v-else-if="tipo == 'input'", :type="type", :placeholder='placeholder', @change="$emit('inputValor', $event.target)")
  textarea.input-control(v-else-if="tipo == 'textarea'", :placeholder='placeholder', @change="$emit('inputValor', $event.target)")
</template>

<script>
export default {
  props: ['tipo', 'placeholder', 'opcoes', 'type'],
  data() {
    return {
      input: this.valor
    }
  }
}
</script>





