<template lang="pug">
  section.painel-ocorrencias
    breadcrumb(:itens="breadcrumb")
    .container
      titulo-internas Minhas ocorrências
      .filtros
        .tipo
          controle-label(:valor="'Tipo da ocorrência'", for='tipoOcorrencia')
          controle-input(:tipo="'select'", :opcoes='opcoes', id='tipoOcorrencia', @inputValor='tipo = $event.value')
        .categoria
          controle-label(:valor="'Categoria do objeto'", for='categoriaObjeto')
          controle-input(:tipo="'select'", :placeholder="'Selecione aqui'", :opcoes='opcoes2', id='categoriaObjeto', @inputValor='filtro.categoria = $event.value')
        .data
          controle-label(:valor="'Data da ocorrência'", for='dataOcorrencia')
          controle-input(:tipo="'input'", :placeholder="'Selecione aqui'", :opcoes='opcoes2', id='dataOcorrencia', @inputValor='filtro.data = $event.value')
        .periodo
          controle-label(:valor="'Período da ocorrência'", for='periodoOcorrencia')
          controle-input(:tipo="'select'", :opcoes='opcoes2', id='periodoOcorrencia', @inputValor='filtro.periodo = $event.value')
        .busca
          controle-label(:valor="'Buscar ocorrência'", for='buscaOcorrencia')
          controle-input(:tipo="'input'", :type="'date'", :placeholder="'Digite aqui'", :opcoes='opcoes', id='buscaOcorrencia', @inputValor='filtro.busca = $event.value')
        .botao
          button.azul Aplicar filtro desejado
      .resultados
        h3 Resultado da sua busca/filtro
        ocorrencia(:ocorrencias="ocorrenciasFiltradas")

</template>

<script>
import Breadcrumb from 'BASICS/Breadcrumb'
import ControleInput from 'BASICS/ControleInput'
import ControleLabel from 'BASICS/ControleLabel'
import Ocorrencia from 'BASICS/Ocorrencia'
import TituloInternas from 'BASICS/TituloInternas'

import BuscaOcorrenciaService from '../../scripts/services/BuscaOcorrenciaService'

export default {
  components: {
    Breadcrumb,
    ControleInput,
    ControleLabel,
    Ocorrencia,
    TituloInternas
  },
  data() {
    return{
      breadcrumb: [
        { nome: 'A' },
        { nome: 'B' }
      ],
      opcoes: [
        'Assalto',
        'Sinal quebrado',
        'Vazamento de água'
      ],
      opcoes2: [],
      ocorrencias: [],
      ocorrenciasFiltradas: [],
      tipo: '',
      filtro: {
        tipo: '',
        categoria: '',
        data: '',
        periodo: '',
        busca: ''
      }
    }
  },
  watch: {
    tipo() {
      this.ocorrenciasFiltradas = []
      this.ocorrencias.filter((ocorrencia) => {
        if(ocorrencia.tipo === this.tipo) {
          this.ocorrenciasFiltradas.push(ocorrencia)
        }
      })
    }
  },
  methods: {
    buscarTodasOcorrencias() {
      BuscaOcorrenciaService.buscarOcorrencias()
        .then(response => {
          for(let ocorrencia in response.data) {
            let ocorrenciaTratada = {
              id: ocorrencia,
              tipo: response.data[ocorrencia].tipo,
              data: response.data[ocorrencia].data,
              endereco: response.data[ocorrencia].endereco,
              turno: response.data[ocorrencia].turno,
              horario: response.data[ocorrencia].horas + 'h' + response.data[ocorrencia].minutos,
              andamento: response.data[ocorrencia].andamento,
              status: null
            }
            if(ocorrenciaTratada.andamento == 'Em andamento') {
              ocorrenciaTratada.status = 'vermelho'
            } else {
              ocorrenciaTratada.status = 'verde'
            }
            this.ocorrencias.push(ocorrenciaTratada)
          }
        })
    }
  },
  mounted() {
    this.buscarTodasOcorrencias()
  }
}
</script>

<style lang="scss">

.painel-ocorrencias {
  .filtros {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;

    .tipo,
    .categoria,
    .data,
    .periodo,
    .busca,
    .botao {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 32%;
      margin-bottom: 20px;

      label {
        margin-bottom: 20px;
      }
    }

    .botao {
      button {
        height: 40px;
        font-size: 16px;
        border-radius: 5px;
      }
    }
  }

  .resultados {
    margin-top: 30px;

    h3 {
      color: $cor-azul-claro;
      font-size: 16px;
      font-weight: normal;
      margin-bottom: 30px;
    }
  }
}

</style>





