import * as urls from '../config'
import axios from 'axios'

export default {
  atualizarOcorrencia (id) {
    let service = urls.API_URL + 'ocorrencias/' + id + '/andamento.json'
    let andamento = '"Resolvida"'
    return axios.put(service, andamento)
    .then(response => {
      return response
    })
    .catch(error => {
      return error
    })
  }
}



