import * as urls from '../config'
import axios from 'axios'

export default {
  buscarOcorrencias () {
    let service = urls.API_URL + 'ocorrencias.json'
    return axios.get(service)
    .then(response => {
      return response
    })
    .catch(error => {
      return error
    })
  }
}



