<template lang="pug">
  section.pesquisar-ocorrencia
    maps-busca-ocorrencia(:tipo='tipo', :periodo='periodo', :categoria='categoria')
    .container
      .sidebar
        titulo-internas Filtrar ocorrências
        form
          .tipo-periodo
            controle-input(:tipo="'select'", :opcoes='tipos', @inputValor='tipo = $event.value')
            controle-input(:tipo="'select'", :opcoes='periodos', @inputValor='periodo = $event.value')
          .categoria
            controle-input(:tipo="'select'", :opcoes='categorias', @inputValor='categoria = $event.value')
        titulo-internas Últimas ocorrências
        ocorrencias-pesquisa(:ocorrencias="ocorrencias")
        titulo-internas Estatísticas
        dia-vs-noite
        router-link(to="/") Ver todas as estatísticas
        .google-display
          p GOOGLE DISPLAY
</template>

<script>
import MapsBuscaOcorrencia from 'BASICS/MapsBuscaOcorrencia'
import TituloInternas from 'BASICS/TituloInternas'
import OcorrenciasPesquisa from 'BASICS/OcorrenciasPesquisa'
import DiaVsNoite from 'BASICS/DiaVsNoite'
import ControleLabel from 'BASICS/ControleLabel'
import ControleInput from 'BASICS/ControleInput'

export default {
  components: {
    MapsBuscaOcorrencia,
    TituloInternas,
    OcorrenciasPesquisa,
    DiaVsNoite,
    ControleLabel,
    ControleInput
  },
  data() {
    return {
      tipos: ['Assalto', 'Sinal quebrado', 'Vazamento de água'],
      periodos: ['Manhã', 'Tarde', 'Noite'],
      categorias: ['A', 'B', 'C'],
      tipo: '',
      periodo: '',
      categoria: '',
      ocorrencias: [
        {
          titulo: 'Briga',
          data: '28 de Maio',
          conteudo: 'Evidentemente, a consolidação das estruturas pode nos levar a considerar a reestruturação do orçamento setorial. Percebemos, cada vez mais, que o início da atividade geral de formação de atitudes cumpre um papel essencial na formulação das diretrizes de desenvolvimento para o futuro.'
        },
        {
          titulo: 'Desentendimento familiar',
          data: '28 de Maio',
          conteudo: 'Evidentemente, a consolidação das estruturas pode nos levar a considerar a reestruturação do orçamento setorial. Percebemos, cada vez mais, que o início da atividade geral de formação de atitudes cumpre um papel essencial na formulação das diretrizes de desenvolvimento para o futuro.'
        }
      ]
    }
  }
}
</script>

<style lang="scss">
  .pesquisar-ocorrencia {
    flex: 1;
    position: relative;

    #map {
      width: 100%;
      height: 100%;
      position: absolute !important;
    }

    .container {
      display: flex;
      justify-content: flex-end;

      .sidebar {
        position: relative;
        margin-top: 30px;
        margin-bottom: 50px;
        background: #fff;
        width: 350px;
        @include media("<tablet") {
          width: 290px;
        }

        h2 {
          border-bottom: 1px solid $cor-cinza-claro;
          padding: 10px 0;
          margin: 0 15px;
        }

        form {
          padding: 20px 15px 10px 15px;

          .tipo-periodo {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;

            select {
              width: 50%;

              &:first-child {
                margin-right: 10px;
              }
            }
          }

          .categoria {
            select {
              width: 100%;
            }
          }
        }

        a {
          background: $cor-cinza-medio;
          display: flex;
          justify-content: center;
          padding: 5px 0;
          margin: 0 15px;
          border-radius: 3px;
          color: #000;

          &:hover,
          &:focus {
            opacity: 0.5;
          }
        }

        .google-display {
          border-top: 1px solid $cor-cinza-medio;
          margin: 20px 15px 0 15px;
          padding: 20px 15px 30px 15px;
          p {
            background: $cor-amarelo;
            text-align: center;
            padding: 100px 0;
          }
        }
      }
    }    
  }
</style>





