import * as urls from '../config'
import axios from 'axios'

export default {
  registrarOcorrencia (ocorrencia) {
    let service = urls.API_URL + '/ocorrencias.json'
    return axios.post(service, ocorrencia)
    .then(response => {
      return response
    })
    .catch(error => {
      return error
    })
  }
}



