<template lang="pug">
  .mapa-busca-ocorrencia
    #map
</template>

<script>
import BuscaOcorrenciaService from '../../scripts/services/BuscaOcorrenciaService'

export default {
  props: ['tipo', 'periodo', 'categoria'],
  data() {
    return {
      map: '',
      ocorrencias: [],
      markers: []
    }
  },
  watch: {
    tipo() {
      for(var i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null)
      }
      this.markers = []
      this.ocorrencias.filter((ocorrencia) => {
        if(ocorrencia.tipo === this.tipo) {
          let marker = new google.maps.Marker({map: this.map, title: ocorrencia.tipo, position: {
            lat: ocorrencia.lat,
            lng: ocorrencia.lng
          }, animation: google.maps.Animation.BOUNCE})
          this.markers.push(marker)
        }
      })
    }
  },
  methods: {
    buscarTodasOcorrencias() {
      BuscaOcorrenciaService.buscarOcorrencias()
        .then(response => {
          for(var ocorrencia in response.data) {
            this.ocorrencias.push(response.data[ocorrencia])
          }
        })
    },
    createMap() {
      let myOptions = {
        zoom: 13,
        center: {
          lat: -5.779696186875594,
          lng: -35.200555348421176
        }
      }
      this.map = new google.maps.Map(document.getElementById('map'), myOptions)
    }
  },
  mounted() {
    this.createMap()
    this.buscarTodasOcorrencias()
  }
}
</script>




