<template lang="pug">
  nav.breadcrumb(aria-label="breadcrumb")
    .container
      ul
        li
          router-link(:to="{ name: 'Index' }", alt="ir para a página inicial", title="Ir para a página inicial") Inicial
        template(v-if="itens.length" v-for="item of itens")
          li.next >
          li(v-if="item.rota")
            router-link(:to="{ name: item.rota }") {{ item.nome }}
          li(v-else) {{ item.nome }}
</template>

<script>
export default {
  props: ['itens']
}
</script>

<style lang="scss">
  .breadcrumb {
    background: $cor-azul-claro;
    padding: 30px 0;
    margin-bottom: 50px;

    ul {
      display: flex;
      list-style: none;

      .next {
        color: #fff;
        margin: 0 30px;
      }
    }
  }
</style>





